import { useEffect, useState } from "react";
import {
  ServiceItem,
  ServiceText,
  ServiceTitle,
  ServicesContainer,
  ServicesSection,
  ServicesTitle,
} from "./styled";
import { HemletForServices } from "./HemletForServices";

export const Services = () => {
  const [show, setShow] = useState(false);
  useEffect(() => {
    setShow(true);
  }, []);

  return (
    <ServicesSection $show={show}>
      <HemletForServices />
      <ServicesContainer>
        <ServicesTitle>Zakres usug</ServicesTitle>
        <main>
          <ServiceItem id="naprawa-pralek">
            <ServiceTitle>Naprawa pralek</ServiceTitle>
            <ServiceText>
              Kompleksowe usugi naprawy pralek automatycznych wszystkich marek.
              Bez&nbsp;wzgldu na&nbsp;to, czy&nbsp;Twoja pralka
              nie&nbsp;pobiera&nbsp;wody, wycieka, nie obraca bbna,
              czy&nbsp;wydaje dziwne dwiki, jestem w stanie zdiagnozowa
              i&nbsp;naprawi problem. Korzystam z nowoczesnych narzdzi
              diagnostycznych oraz oryginalnych czci zamiennych,
              co&nbsp;zapewnia trwao i&nbsp;niezawodno wykonanej naprawy.
              Moim celem jest przywrcenie penej funkcjonalnoci Twojej pralki
              w&nbsp;jak najkrtszym czasie.
            </ServiceText>
          </ServiceItem>
          <ServiceItem id="naprawa-zmywarek">
            <ServiceTitle>Naprawa zmywarek</ServiceTitle>
            <ServiceText>
              Specjalistyczna naprawa zmywarek rnych marek, gwarantujca ich
              dugotrwae i&nbsp;efektywne dziaanie. Problemy takie jak brak
              podgrzewania wody, wycieki, problemy z&nbsp;pomp, czy problemy
              z&nbsp;suszeniem naczy to tylko niektre z usterek, ktre
              naprawiam. Oferuj szybkie i&nbsp;skuteczne usugi naprawcze,
              zapewniajc, e&nbsp;Twoja zmywarka bdzie dziaa jak nowa.
              Dziki regularnym szkoleniom i&nbsp;aktualizacji wiedzy, jestem
              na&nbsp;bieco z&nbsp;najnowszymi technologiami i&nbsp;metodami
              napraw.
            </ServiceText>
          </ServiceItem>
          <ServiceItem id="naprawa-ekspresw">
            <ServiceTitle>Naprawa ekspresw do kawy</ServiceTitle>
            <ServiceText>
              Profesjonalna naprawa ekspresw do&nbsp;kawy, niezalenie od marki
              i&nbsp;modelu. Czy Twj ekspres do kawy przesta dziaa, nie
              zaparza kawy jak&nbsp;wczeniej, wywietla bdy, czy ma inne
              problemy? Jestem tutaj, aby pomc. Specjalizuj si
              w&nbsp;naprawie zarwno domowych, jak i&nbsp;profesjonalnych
              ekspresw do kawy, oferujc precyzyjn diagnostyk i szybkie
              naprawy. Moim celem jest, aby mg cieszy si swoj ulubion
              kaw bez adnych przerw.
            </ServiceText>
          </ServiceItem>
          <ServiceItem id="naprawa-telewizorw">
            <ServiceTitle>Naprawa telewizorw</ServiceTitle>
            <ServiceText>
              Fachowa naprawa telewizorw wszystkich typw i&nbsp;marek,
              od&nbsp;tradycyjnych po nowoczesne modele smart. Naprawiam
              rnorodne problemy, takie jak brak obrazu, brak dwiku, problemy
              z&nbsp;zasilaniem, uszkodzone matryce i&nbsp;inne. Dziki
              dostpowi do oryginalnych czci zamiennych oraz nowoczesnemu
              sprztowi diagnostycznemu, jestem w&nbsp;stanie szybko
              i&nbsp;skutecznie przywrci pen funkcjonalno Twojego
              telewizora. Gwarantuj precyzyjn i&nbsp;profesjonaln obsug,
              aby mg cieszy si ulubionymi programami bez zakce.
            </ServiceText>
          </ServiceItem>
          <ServiceItem>
            <ServiceTitle>Serwis i&nbsp;konserwacja</ServiceTitle>
            <ServiceText>
              wiadcz kompleksowe usugi serwisowe i&nbsp;konserwacyjne
              urzdze&nbsp;AGD. Zapewniam szybk i&nbsp;skuteczn napraw
              sprztu, dbajc o&nbsp;to, aby&nbsp;dziaa jak&nbsp;nowy.
              Regularna konserwacja przeprowadzana przez dowiadczonego
              specjalist pozwala na&nbsp;przeduenie ywotnoci urzdze
              i&nbsp;zapobieganie awariom.
            </ServiceText>
          </ServiceItem>
          <ServiceItem>
            <ServiceTitle>Konsultacje i&nbsp;doradztwo</ServiceTitle>
            <ServiceText>
              Oferuj profesjonalne konsultacje i&nbsp;doradztwo w&nbsp;zakresie
              serwisu urzdze&nbsp;AGD. Dziki wieloletniemu dowiadczeniu,
              pomagam zidentyfikowa problemy z&nbsp;urzdzeniami
              oraz&nbsp;doradzam najlepsze rozwizania, aby&nbsp;zapewni
              ich&nbsp;sprawne dziaanie. Gwarantuj rzeteln diagnoz
              oraz&nbsp;fachowe porady dotyczce napraw i&nbsp;konserwacji
              sprztu.
            </ServiceText>
          </ServiceItem>
        </main>
      </ServicesContainer>
    </ServicesSection>
  );
};
