import { AboutContainer, AboutSection, AboutText, AboutTitle } from "./styled";
import { TeamImage } from "../../common/TeamImage";
import team from "../../assets/images/serwis.jpg";
import LazyLoad from "react-lazyload";
import { useEffect, useState } from "react";
import { HelmetForAbout } from "./HelmetForAbout";

export const About = () => {
  const [show, setShow] = useState(false);
  useEffect(() => {
    setShow(true);
  }, []);

  return (
    <AboutSection $show={show}>
      <HelmetForAbout />
      <AboutContainer>
        <AboutTitle>Kim jestem</AboutTitle>
        <main>
          <AboutText>
            Jestem specjalist z&nbsp;wieloletnim dowiadczeniem w&nbsp;naprawie
            pralek automatycznych, zmywarek, telewizorw i&nbsp;ekspresw
            do&nbsp;kawy. Podchodz do&nbsp;kadej naprawy z&nbsp;penym
            zaangaowaniem i&nbsp;pasj. Moim celem jest zapewnienie najwyszej
            jakoci usug oraz&nbsp;penej satysfakcji klientw. Dziki
            indywidualnemu podejciu do&nbsp;kadego zlecenia, mog skupi si
            na&nbsp;szczegach i&nbsp;dostarcza rozwizania dopasowane
            do&nbsp;specyficznych potrzeb kadego klienta. Wierz, e&nbsp;kada
            naprawa to&nbsp;nie tylko techniczne wyzwanie, ale&nbsp;rwnie
            szansa na&nbsp;budowanie trwaych relacji z&nbsp;klientami, ktrzy
            mog liczy na&nbsp;rzetelno, terminowo i&nbsp;uczciwo.
            Zachcam do&nbsp;skorzystania z&nbsp;moich
            usug&nbsp;&nbsp;gwarantuj, e&nbsp;Twoja pralka lub&nbsp;ekspres
            do&nbsp;kawy bdzie dziaa jak&nbsp;nowy!
          </AboutText>
          <LazyLoad>
            <TeamImage src={team} alt="serwis" />
          </LazyLoad>
        </main>
      </AboutContainer>
    </AboutSection>
  );
};
