import { Helmet, HelmetProvider } from "react-helmet-async";

export const HemletForTestimonials = () => (
  <HelmetProvider>
    <div>
      <Helmet>
        <title>Opinie klientw | Serwis RTV AGD w Przemylu</title>
        <meta
          name="description"
          content="Przeczytaj opinie moich zadowolonych klientw o moim serwisie pralek, zmywarek, ekspresw do kawy oraz telewizorw w Przemylu."
        />
        <meta
          name="keywords"
          content="opinie, recenzje, serwis, RTV, AGD, Przemyl, pralki, zmywarki, ekspresy do kawy, telewizory"
        />
        <link rel="canonical" href="https://naprawaprzemysl.pl/opinie" />
        <meta
          property="og:title"
          content="Opinie klientw | Serwis RTV AGD w Przemylu"
        />
        <meta
          property="og:description"
          content="Sprawd opinie naszych klientw na temat serwisu RTV i AGD w Przemylu."
        />
        <meta property="og:url" content="https://naprawaprzemysl.pl/opinie" />
      </Helmet>
    </div>
  </HelmetProvider>
);
