import { Helmet, HelmetProvider } from "react-helmet-async";

export const HelmetForAbout = () => {
  return (
    <HelmetProvider>
      <div>
        <Helmet>
          <title>O mnie | Serwis RTV AGD w Przemylu</title>
          <meta
            name="description"
            content="Dowiedz si wicej o mnie i moim dowiadczeniu w naprawie pralek, zmywarek, ekspresw do kawy oraz telewizorw w Przemylu."
          />
          <meta
            name="keywords"
            content="o mnie, serwis, RTV, AGD, Przemyl, pralki, zmywarki, ekspresy do kawy, telewizory"
          />
          <link rel="canonical" href="https://naprawaprzemysl.pl/o-mnie" />
          <meta
            property="og:title"
            content="O mnie | Serwis RTV AGD w Przemylu"
          />
          <meta
            property="og:description"
            content="Dowiedz si wicej o mnie i moim dowiadczeniu w naprawie pralek, zmywarek, ekspresw do kawy oraz telewizorw w Przemylu."
          />
          <meta property="og:url" content="https://naprawaprzemysl.pl/o-mnie" />
        </Helmet>
      </div>
    </HelmetProvider>
  );
};
